.container{
  position: relative;
  text-align: right;
  min-height: 616px;
}

.text{
  padding: 100px 1.5em 0px 0px;
}

.text h1{
  margin: 0px;
}

.text p{
  margin-top: 10px;
}

@media (max-width: 700px){
  .container{min-height: 316px;}
}
@media (max-width: 500px){
  .container{min-height: 216px}
}
@media (min-width: 1000px){
  .container{min-height: 616px;}
}