.container{
  position: relative;
}

.container h1{
  margin: 0px 25px 15px 0px;
}
.header{
  padding: 2px 10px;
}
.container p{
  width: 40%;
  font-size: 0.8em;
  margin-top: 0.5em;
}
.text{  
  padding: 100px 0px 0px 1.5em;
}
@media (max-width: 600px ){
  .container{min-height: 316px;}
}
@media (min-width: 600px) and (max-width: 900px ){  
  .container{min-height: 416px;}
}
@media (min-width: 900px ){  
  .container{min-height: 616px;}
}