*{margin: 0;}
html{
  font-weight: 100;
  font-size: 14px;
}
body{
  background: black;
  color: white;
}
@font-face {
  font-family: 'Archangelsk';
  src: local('Archangelsk'), url(./myArchangelsk.ttf) format('woff');
}

.globContainer{
  margin: auto;
  padding-top: 50px;
  width: 965px;
}

.globContainer *{
  box-sizing: border-box;
  font-family: 'Archangelsk';
}

.little{font-size: 1rem;}
.medium{font-size: 1.2rem;}
.big{font-size: 1.4rem;}
.bigger{font-size: 2rem;}

@media (min-width: 300px ){
  .globContainer{width: 100%;}  
}
@media (min-width: 600px){
  .globContainer{width: 600px;}  
  html {font-size: 16px;}
}
@media (min-width: 1000px ){
  .globContainer{width: 965px;}
  html {font-size: 24px;}
}