.defaultLnk{
  cursor: pointer;
  position: relative;
  white-space: nowrap;
}
.BG{
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: -1;
  height: 1em;
  width: 110%;
  margin-left: -5%;
  padding: 5px 10px;
  transform: skewX(-30deg);
}
.defaultLnk:hover .BG{
  background:#cbd224;
}

.activeLnk .BG{
  background: #dbe244;
}