.menu{
  font-size: 1.75em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 100%;
  position: fixed;
  top: 0px;
  left: 0px;  
  background: black;
  color: white;
  padding: 5px 0px;
  z-index: 100;
}
.dash{
  padding: 0px 10px;
}

@media (max-width: 700px){
  .menu{font-size: 1.4em;}
}
@media (max-width: 500px){
  .menu{font-size: 1em;}
  .dash{padding: 0px 2px;}
}
@media (min-width: 1000px){
  .menu{font-size: 1.75em;}
}