.container{
  position: relative;
  text-align: right;
}

.container h1{
  margin: 0px 25px 15px 0px;
}

.text{
  padding: 50px 1em 0px 0px;
}

.text p{
  margin-top: 15px;
}

.header{
  padding: 3px 10px;
}

@media (max-width: 600px ){
  .container{min-height: 316px;}
}
@media (min-width: 600px) and (max-width: 900px ){  
  .container{min-height: 416px;}
}
@media (min-width: 900px ){  
  .container{min-height: 616px;}
}